<script>
import { mapGetters, mapActions } from 'vuex'
import TradingStrategy from '@/components/TradingStrategy.vue'

export default {
  name: 'TradingPage',
  components: {
    TradingStrategy
  },
  data() {
    return {
      recentOrders: [], // Ïã§Ï†úÎ°úÎäî APIÏóêÏÑú Í∞ÄÏ†∏Ïò® Îç∞Ïù¥ÌÑ∞
      hasOrders: false
    }
  },
  computed: {
    ...mapGetters('trading', ['isTrading', 'currentStrategy', 'isLoading'])
  },
  async created() {
    await this.loadTradingStatus()
    // Îß§Îß§ Ïù¥Î†• Î°úÎìú (Ï∂îÌõÑ Íµ¨ÌòÑ)
    // await this.loadTradingHistory()
  },
  methods: {
    ...mapActions('trading', ['loadTradingStatus']),
    
    formatDateTime(dateString) {
      if (!dateString) return '-'
      
      try {
        const date = new Date(dateString)
        return date.toLocaleString('ko-KR', {
          month: 'short',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      } catch (error) {
        return dateString
      }
    },
    
    getStatusText(status) {
      const statusMap = {
        'SUCCESS': 'ÏÑ±Í≥µ',
        'FAILED': 'Ïã§Ìå®',
        'PENDING': 'ÎåÄÍ∏∞',
        'CANCELLED': 'Ï∑®ÏÜå'
      }
      return statusMap[status] || status
    }
  }
}
</script>

